/* ************HOME PAGE CSS*********** */
.home-top-section{
    min-height: 546px;
    background: lightgoldenrodyellow;
}
.home-top-info{
    position: relative;

}
.home-top-svg{
    display: flex;
    align-items: center;
    justify-content: center;
}
.home-top-svg img{
    width: 100%;
}

.home-top-info ul{
    list-style: none;
    padding-left: 0;
}
.home-top-info-position{
    position: absolute;
    bottom: 140px;
    left: 40px;
}
.home-top-info-btn button:nth-child(1){
    margin-right: 20px;
}
.purposeofwebapp-section{
    height: 200px;
    padding-top: 30px;
}
.purposeofwebapp{
    display: block;
    
}
.purposeofwebapp h3{
    font-size: 25px !important;
}
.card-info{
    display: flex;
    margin: auto;
}
.card-hover-info{
    padding-top: 10px;
    /* border: 1px solid black; */
    text-align: center;
    border-radius: 5px;
    margin-top: 10px;
    color: black;
    transition: visibility 0s, opacity 1.5s linear;
    opacity: 0;
}
.home-feature-collection{
    min-height: 560px;
}
.home-feature-Col01{
    position: relative;
}
.contain-feature-img-male{
    position: absolute;
}
.contain-feature-img-male img{
    width: 90%;
    height: 100%;
}
.contain-feature-info-male{
    position: absolute;
    position: absolute;
    top: 20px;
    left: 30px;
    font-size: small;
}
.contain-feature-female{
    width: 100%;
    height: 50%;
    position: relative;
}
.contain-feature-female-img{
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
}
.contain-feature-female-img img{
    width: 60%;
    height: 80%;
    margin: auto;
}
.contain-feature-female-info{
    position: absolute;
    top: 23%;
    left: 118px;
    margin: auto;
}
.contain-feature-bag{
    position: relative;
    width: 50%;
    height: 50%;
    float: left;
}
/* .contain-feature-bag-img{
    width: 100%;
    height: 100%;
    position: absolute;
} */
/* .contain-feature-bag-img img{} */

.contain-feature-bag-info{
    position: absolute;
    top: 20px;
    left: 20px;
}

.contain-feature-cup{
    position: relative;
    width: 50%;
    height: 50%;
    float: right;
}
/* .contain-feature-cup-img{} */
.contain-feature-cup-info{
    position: absolute;
    top: 20px;
    left: 20px;
}













